@import "../styles/defaults";

.container, .rotation_div, .move_div {
  width: 100%;
  height: 100vh;
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: 100%;
  align-items: center;
  grid-area: 1 / 1 / 2 / 2;
  justify-self: center;
  position: relative;
  will-change: transform;
  -webkit-filter: blur(0.000001px); }

.container {
  position: absolute;
  overflow: hidden; }

.move_div {
  overflow: hidden; }

.content {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  will-change: transform;

  & > div {}

  & > section {
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 60vw;
    @include for-phone-only {
      width: 90vw; } }

  a {
    padding: 0 .3em; }

  h2 {
    color: $text-color; }

  h1 {
    color: $secondary; } }

.up, .down {
  position: absolute;
  color: $text-color-light;
  cursor: pointer;
  font-size: 2rem;
  z-index: 999;
  transition: color .3s ease-in;

  &:hover {
    color: $secondary; } }

.down {
  top: calc(50% + 45vh); }

.up {
  bottom: calc(50% + 45vh); }


.dot {
  position: absolute;
  left: 50%;
  right: 50%;
  width: 10px;
  height: 10px;
  background: red; }
